<template>
  <md-table v-model="sprints" md-sort="name" md-sort-order="asc">
    <md-table-toolbar>
      <h1 class="md-title">Sprints</h1>
    </md-table-toolbar>
    <md-table-row slot="md-table-row" slot-scope="{ item }">
      <md-table-cell md-label="ID" md-numeric>{{ item.id }}</md-table-cell>
      <md-table-cell md-label="Name" md-numeric>{{ item.name }}</md-table-cell>
      <md-table-cell md-label="Start Date" md-numeric>{{ item.startDate }}</md-table-cell>
      <md-table-cell md-label="Name" md-sort-by="name">{{ item.endDate }}</md-table-cell>
      <md-table-cell md-label="Assignee" md-numeric>{{ item.assignee }}</md-table-cell>
    </md-table-row>
  </md-table>
</template>

<script>
import { getSprintsForProject } from "../services/scrum-board-api";

export default {
    name: "Sprints",
    props: {
        projectId: { type: Number, required: true }
    },
    data() {
        return {
            sprints: []
        }
    },
    methods: {
        onRouterPush(id) {
            this.$router.push({ path: `/sprints/${id}` })
        }
    },
    async mounted() {
        const { data: sprints } = await getSprintsForProject(this.projectId);
        this.sprints = sprints;
    }
}
</script>
