<template>
  <md-table v-model="backlog" md-sort="name" md-sort-order="asc">
    <md-table-toolbar>
      <h1 class="md-title">Backlog</h1>
    </md-table-toolbar>
    <md-table-row slot="md-table-row" slot-scope="{ item }">
      <md-table-cell md-label="ID" md-numeric>{{ item.id }}</md-table-cell>
      <md-table-cell md-label="Type" md-numeric>{{ item.type }}</md-table-cell>
      <md-table-cell md-label="Priority" md-numeric>{{ item.priority }}</md-table-cell>
      <md-table-cell md-label="Name" md-sort-by="name"><a href="" @click.prevent="onRouterPush(item.id)">{{ item.title }}</a></md-table-cell>
      <md-table-cell md-label="Assignee" md-numeric>{{ item.assignee }}</md-table-cell>
    </md-table-row>
  </md-table>
</template>

<script>
import { getBacklogForProject } from "../services/scrum-board-api";

export default {
    name: "Backlog",
    props: {
        projectId: { type: Number, required: true }
    },
    data() {
        return {
            backlog: []
        }
    },
    methods: {
        onRouterPush(id) {
            this.$router.push({ path: `/tasks/${id}` })
        }
    },
    async mounted() {
        const { data: backlog } = await getBacklogForProject(this.projectId);
        this.backlog = backlog;
    }
}
</script>
