<template>
  <md-table v-model="projects" md-sort="name" md-sort-order="asc">
    <md-table-toolbar>
      <h1 class="md-title">Projects</h1>
    </md-table-toolbar>
    <md-table-row slot="md-table-row" slot-scope="{ item }">
      <md-table-cell md-label="ID" md-numeric>{{ item.id }}</md-table-cell>
      <md-table-cell md-label="Name" md-sort-by="name"><a href="" @click.prevent="onRouterPush(item.id)">{{ item.name }}</a></md-table-cell>
    </md-table-row>
  </md-table>
</template>

<script>
  import { getProjects } from "../services/scrum-board-api";

  export default {
      name: 'Projects',
      data: () => ({
          projects: []
      }),
      async mounted() {
          const { data: projects } = await getProjects();
          this.projects = projects;
      },
      methods: {
          onRouterPush(id) {
              this.$router.push({ path: `/projects/${id}` })
          }
      }
  }
</script>
