import Projects from '../components/Projects'
import Backlog from '../components/Backlog'
import Sprints from '../components/Sprints'
import ProjectSidebar from '../components/ProjectSidebar'
import Tasks from '../components/Tasks'
import Project from '../components/Project'
import User from '../components/User'
import CreateIssueDialog from '../components/CreateIssueDialog'
import CreateUserDialog from '../components/CreateUserDialog'


const backlog = {
    path: 'backlog',
    component: Backlog,
    props: route => ({
        projectId: parseInt(route.params.id, 10) || undefined
    })
};
const sprints = {
    path: 'sprint',
    component: Sprints,
    props: route => ({
        projectId: parseInt(route.params.id, 10) || undefined
    })
};


export const routes = [
    { path: '/projects', component: Projects },
    { path: '/tasks', component: Tasks },
    {
        path: '/projects/:id',
        components: {
            default: Project,
            sidebar: ProjectSidebar,
        },
        props: {
            default: route => ({
                projectId: parseInt(route.params.id, 10) || undefined
            }),
            sidebar: route => ({
                projectId: parseInt(route.params.id, 10) || undefined
            })
        },
        children: [backlog, sprints]
    },
    { path: '/users/:id', component: User, props: (route) => ({ userId: parseInt(route.params.id, 10) }) },
    { path: '/create-issue', component: CreateIssueDialog },
    { path: '/create-user', component: CreateUserDialog },
];
